.servicecard {
  z-index: 100;
  width: 100%;
  height: 10.5rem;
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  padding-right: 2rem;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2);
  border-radius: 0.5rem;
  padding: 1.25rem;
  &__container {
    display: flex;
  }
  &__image {
    width: 3.125rem;
    height: 3.125rem;
    border-radius: 50%;
    margin-right: 1rem;
    object-fit: contain;
  }
  &__content {
    width: 100%;
    display: flex;
    flex-direction: column;
    p {
      margin: 0;
      text-align: left;
    }
  }
  &__title {
    width: 100%;
    font-size: 1.25rem;
    margin-bottom: 0.5rem;
  }
  &__subtitle {
    font-size: 0.75rem;
  }
  &__description {
    margin-top: 1rem;
  }
}

@media screen and (min-width: 48rem) {
  .servicecard {
    width: 25rem;
    height: 9.5rem;
  }
}
