@import "../../utils/fonts.scss";
.dashboard-container {
  display: flex;
  flex-direction: column;
  margin-top: 1.25rem;
  &__image {
    width: 100%;
    height: 25rem;
  }
  &__title {
    max-width: 100%;
  }
  &__content {
    max-width: 100%;
  }
}
.signin-container {
  display: flex;
  flex-direction: column-reverse;
}
@media screen and (min-width: 48rem) {
  .dashboard-container {
    display: flex;
    flex-direction: row;
    &__image {
      width: 37.5rem;
      height: 31.25rem;
    }
    &__content {
      margin-left: 6rem;
      margin-top: 6rem;
    }
    &__title {
      color: #000080;
      max-width: 70%;
      font-size: 1.625rem;
      font-family: $font-semibold;
    }
    &__description {
      margin-top: 1rem;
      color: #999999;
      max-width: 80%;
    }
  }
  .signin-container {
    display: flex;
    flex-direction: row;
  }
}
